import {isMobile} from "./DeviceUtil";//判断是否为移动设备
import axios from 'axios';
import {browserType} from "./DeviceUtil";//浏览器判断

/**
 * 调用原生方法
 * @type {{call: CallService.call, initUser: CallService.initUser}}
 */
export const CallService = {

  /**
   * 调用原生
   * @param controller
   * @param param
   * @returns {Promise<any>}
   */
  call: function (controller, param) {
  	alert('走进调用')
    return new Promise((resolve, reject) => {
      //如果是浏览器打开，默认全部走代理请求
      if (!isMobile()) {
      	alert("浏览器请求开启")
        callByProxyServer(controller, param, (successResult) => {
          resolve(successResult)
        }, (error) => {
          reject(error)
        })
        //如果是移动应用平台版本
      } else{
      	alert('链容空间请求开启')
        callLRKJHelper(controller, param, (successResult) => {
          resolve(successResult)
        }, (error) => {
          reject(error)
        })

      }
    })
  },

  /**
   * 初始化用户信息，存入localStorage
   * @returns {Promise<any>}
   */
  initUser: function () {
    return new Promise((resolve, reject) => {
      if (!isMobile()) {
        var result={//浏览器调试写一些固定的配置项例如：与后端保持一致
        	usercode:'00030',
        	userid:'3423ewasdeae3',
        	url:'http://125.35.5.116:8085'
        }
		localStorage.setItem("userinfo", JSON.stringify(result));
      } else {
        //initSummer() //初始化summer
        let params = {
          "params": {
            "transtype": "request_token"
          },
          "callback": function (res) {
            if (typeof (res.result) === 'string') {
              res.result = JSON.parse(res.result);
              localStorage.setItem("userinfo", res.result);
            }
          },
          "error": function (err) {
            alert('从原生获取用户信息失败' + JSON.stringify(err));
            reject(err)
          }
        };
        //调用原生
        summer.callService("SummerService.gotoNative", params, false);
      } 
    })
  },
}

