//调用移动应用平台
const callLRKJHelper = function (controller, param, successCall, errorCall) {
    let Appid = 'fssc_mobile_approva'//请求前检查是否是该应用的Appid;
    let http = 'http://125.35.5.116:8085';//开发的时候写，上线的时候传空;ma
    let params = {
      params: {
        "transtype": "serviceCall",
        "innerParams": {
          param: param,
          maserver: http,
        },
        appid: Appid,
        controllerid: controller,//公用的controller;
      },
      callback: function (result) {
        if (typeof (result) === 'string') {
          result = JSON.parse(result)
        }
        successCall(result)
      }, //成功回调函数
      error: function (error) {
        alert(JSON.stringify(error))
        errorCall(error)
      } //失败回调函数
    }
    summer.callService("SummerService.gotoNative", params, false);
}
