import Vue from 'vue';
import Router from 'vue-router';

import Move from '@/components/move/move.vue';//打卡首页
import Detailslist from '@/components/detailslist/details.vue';//记录
import Statistics from '@/components/statistics/statistics.vue';//统计

Vue.use(Router)
export default new Router({  
  routes: [
  	{
  	  path: '/',
      name: '/Move',
      component: Move
  	},
    {
      path: '/Detailslist',
      name: '/Detailslist',
      component: Detailslist
    },
    {
      path: '/Statistics',
      name: '/Statistics',
      component: Statistics
    }
    
  ]
})



